const convertToKorean = (name) => {
    const conversionMap = {
        "pedro_ivory": " ",
        "pedro_gray": " ",
        "pedro_blue": " ",
        "basic": "",
        "winged": "",
        "finished": "",
        "fabric": ""
    };
    return conversionMap[name] || name;
};





